import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { Recommendations } from '../../stores/recommendations';


const Test = () => {
    useEffect(() => {
        fetch(`/data/recommendations.json`, {
            method:   'GET',
            headers:  {
            'Content-Type': 'application/json',
            }
            })
            .then(response => response.json())
            .then(data => {
                //console.log('recommendations: ' + JSON.stringify(data))
                Recommendations.ProcessRecommendations(data)
            })
        
    }, [Stores.deduction.deduction.value, Stores.income.agi.value, Stores.taxcredit.total_tax_credit.value]);

    return <div style="padding:10px;"></div>
}

export default Test;